<template>
  <div class="entity-search-dialog">
    <el-dialog
      :title="dialogTitle"
      :destroy-on-close="true"
      :visible.sync="dialogVisible_"
      :close-on-click-modal="false"
      @close="isClosedDialog"
      width="800px"
    >
      <div class="dialog-form-search">
        <portal-form
          id="dialogForm"
          ref="dialogFormRef"
          :form-data="dialogForm"
          :form-rules="dialogFormRules"
          :form-config-list="formConfigList"
          :searchBtnLoading="searchLoading"
          :smallAutoColumn="smallAutoColumn"
          :bigAutoColumn="bigAutoColumn"
          @common-enter-event="searchEntity"
          @reset-btn-click="resetForm"
          @search-btn-click="searchEntity"
        >
        </portal-form>
      </div>

      <div class="dialog-table-list margin-top-20">
        <table-page
          size="small"
          :isCheck="true"
          :isCheckMemory="false"
          :isSingleCheck="true"
          :isSearch="isSearchTable"
          :columns="dialogTableColumns"
          :getTableData="getDialogTableData"
          :initializingLoading="false"
          :pagination="pagination"
          @single-selection-change="handleTableSelection"
        >
          <template slot="status" slot-scope="scope">
            <div class="flex flex-align">
              <span
                v-if="scope.data.row.status !== ''"
                class="status-circle"
                :style="statusStyle(scope.data.row.status)"
              >
              </span>
              <span>{{ showStatus(scope.data.row.status) }}</span>
            </div>
          </template>
        </table-page>
      </div>
      <span slot="footer" class="dialog-footer">
        <div class="option-buttons">
          <el-button size="small" @click="isOk" type="primary">{{ $t('common.button.confirm') }} </el-button>
          <el-button plain size="small" @click="isClosedDialog">{{ $t('common.button.cancel') }}</el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { queryBusinessCustomerList } from '@/views/identifier-center/api/api-customer';
import { queryOrgListByPage } from '@/views/identifier-center/api/api-organization';
import { queryBusinessGroupList } from '@/views/identifier-center/api/api-group';
import { queryCustomerIdentity } from '@/views/identifier-center/api/api-customer';
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage/index.vue';
export default {
  name: 'EntitySearchDialog',
  props: {
    smallAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    dialogTitle: {
      type: String,
      default: '',
    },
    fieldName: {
      type: String,
      default: '',
    },
    dialogVisible: {
      type: Boolean,
      default: false,
    },
  },
  components: { PortalForm, TablePage },
  data() {
    return {
      identityStatus: [
        { label: 'All', value: '' },
        { label: 'Active', value: '03' },
        { label: 'Dormant', value: '08' },
        { label: 'Frozen', value: '05' },
        { label: 'New', value: '00' },
        { label: 'Suspended', value: '04' },
      ],
      status: [{ label: 'All', value: '' }],
      dialogForm: {
        shortCode: '',
        msisdn: '',
        bankName: '',
        bankShortCode: '',
        groupCode: '',
        groupName: '',
      },
      dialogFormRules: {},
      searchLoading: false,
      isSearchTable: false,
      selectedData: {},
    };
  },
  computed: {
    formConfigList() {
      return this.computedForm();
    },

    dialogTableColumns() {
      return this.computedTableColumns();
    },

    dialogVisible_: {
      get() {
        return this.dialogVisible;
      },
      set() {
        this.isClosedDialog();
      },
    },

    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return { 'background-color': '#658DF9' };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '05':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '08':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          default:
            return;
        }
      };
    },
    pagination() {
      return {
        pageIndex: 1,
        pageSize: 5,
        layout: 'total, prev, pager, next',
      };
    },
  },
  methods: {
    computedForm() {
      let tempForm;
      switch (this.fieldName) {
        case 'msisdn':
          tempForm = [
            {
              label: 'MSISDN',
              key: 'msisdn',
              comp: 'el-input',
              bind: {
                placeholder: 'Please enter',
              },
            },
          ];
          break;
        case 'shortCode':
          tempForm = [
            {
              label: 'Short Code',
              key: 'shortCode',
              comp: 'el-input',
              bind: {
                id: 'identityId',
                placeholder: 'Please enter',
                maxlength: 21,
              },
            },
            {
              label: 'Organization Name',
              key: 'organizationName',
              comp: 'el-input',
              bind: {
                placeholder: 'Please enter',
              },
            },

            {
              label: 'Identity Status',
              key: 'status',
              comp: 'el-select',
              options: this.identityStatus,
              bind: {
                id: 'identityId',
                placeholder: 'Please select',
                maxlength: 21,
              },
            },
          ];
          break;
        case 'groupCode':
          tempForm = [
            {
              label: 'Group Code',
              key: 'groupCode',
              comp: 'el-input',
              bind: {
                placeholder: 'Please enter',
              },
            },
            {
              label: 'Group Name',
              key: 'groupName',
              comp: 'el-input',
              bind: {
                placeholder: 'Please enter',
              },
            },
            {
              label: 'Status',
              key: 'status',
              comp: 'el-select',
              options: this.status,
              bind: {
                id: 'identityId',
                placeholder: 'Please enter',
                maxlength: 21,
              },
            },
          ];
          break;
      }
      return tempForm;
    },
    computedTableColumns() {
      let tempColumns;
      switch (this.fieldName) {
        case 'msisdn':
          tempColumns = [
            { label: 'MSISDN', prop: 'msisdn', align: 'left' },
            { label: 'Customer Type', prop: 'customerType', align: 'left', __slotName: 'customerType' },
            { label: 'Status', prop: 'status', align: 'left', __slotName: 'status' },
          ];
          break;
        case 'shortCode':
          tempColumns = [
            { label: 'Short Code', prop: 'shortCode', align: 'left' },
            { label: 'Organization Name ', prop: 'organizationName', align: 'left' },
            { label: 'Status', prop: 'status', align: 'left', __slotName: 'status' },
          ];
          break;
        case 'groupCode':
          tempColumns = [
            { label: 'Group Code', prop: 'groupCode', align: 'left' },
            { label: 'Group Name', prop: 'groupName', align: 'left' },
            { label: 'Status', prop: 'status', align: 'left', __slotName: 'status' },
          ];
          break;
      }
      return tempColumns;
    },

    isClosedDialog() {
      this.$refs['dialogFormRef'].innerFormRef.resetFields();
      this.selectedData = {};
      this.pagination.pageIndex = 1;
      this.$emit('onCloseDialog', false);
    },

    searchEntity() {
      this.$refs['dialogFormRef'].innerFormRef.validate((valid) => {
        if (valid) {
          this.isSearchTable = true;
          this.searchLoading = true;
        } else {
          this.searchLoading = false;
          return false;
        }
      });
    },

    resetForm() {
      this.$refs['dialogFormRef'].innerFormRef.resetFields();
    },

    getDialogTableData(queryParams) {
      switch (this.fieldName) {
        case 'msisdn':
          return new Promise((resolve) => {
            this.getCustomerList(resolve);
          });
        case 'shortCode':
          return new Promise((resolve) => {
            this.getOrgList(queryParams, resolve);
          });
        case 'groupCode':
          return new Promise((resolve) => {
            this.getGroupList(queryParams, resolve);
          });
      }
    },

    getOrgList(queryParams, resolve) {
      queryParams.shortCode = this.dialogForm.shortCode;
      queryParams.status = this.dialogForm.status;
      queryParams.organizationName = this.dialogForm.organizationName;
      queryParams.pageSize = this.pagination.pageSize;
      queryOrgListByPage(queryParams)
        .then((res) => {
          if (res.header && res.header.success) {
            let tempRes = {
              header: res.header,
              list: res.list || [],
              totalRecords: res.totalRecord,
            };
            resolve(tempRes);
          } else {
            resolve();
          }
        })
        .catch(() => {
          resolve();
        })
        .finally(() => {
          this.isSearchTable = false;
          this.searchLoading = false;
        });
    },

    async getCustomerList(resolve) {
      let customerParams = {};
      let customerListParams = {
        msisdn: this.dialogForm.msisdn,
        page: this.pagination.pageIndex,
        pageSize: this.pagination.pageSize,
      };
      const customerIdRes = await queryCustomerIdentity({ msisdn: this.dialogForm.msisdn });
      if (customerIdRes.header && customerIdRes.header.success) {
        if (customerIdRes.identityId) {
          customerParams.customerId = customerIdRes.identityId;
          customerParams.cpId = customerIdRes.cpId;
        } else {
          this.$modal.msgError('The MSISDN does not exist');
          resolve();
        }
      } else {
        resolve();
      }
      let customerListRes = null;
      try {
        customerListRes = await queryBusinessCustomerList(customerListParams);
      } catch (e) {
        resolve();
      } finally {
        if (customerListRes.header && customerListRes.header.success) {
          let customerResult = {
            header: customerListRes.header,
            list: customerListRes.customerList || [],
            totalRecords: customerListRes.total,
          };
          resolve(customerResult);
        } else {
          resolve();
          this.$modal.msgError(customerListRes.header.responseDesc);
        }
        this.isSearchTable = false;
        this.searchLoading = false;
      }
    },
    getGroupList(queryParams, resolve) {
      let params = {
        groupCode: this.dialogForm.groupCode,
        groupName: this.dialogForm.groupName,
        pageSize: this.pagination.pageSize,
        pageNo: this.pagination.pageIndex,
      };
      queryBusinessGroupList(params)
        .then((res) => {
          if (res.header && res.header.success) {
            let tempRes = {
              header: res.header,
              list: res.list || [],
              totalRecords: res.totalRecords,
            };
            resolve(tempRes);
          } else {
            resolve();
          }
        })
        .catch(() => {
          resolve();
        })
        .finally(() => {
          this.isSearchTable = false;
          this.searchLoading = false;
        });
    },

    handleTableSelection(row) {
      this.selectedData = row;
    },

    isOk() {
      if (JSON.stringify(this.selectedData) === '{}') {
        return;
      }
      this.$emit('select', this.selectedData);
      this.isClosedDialog();
    },

    showStatus(val) {
      switch (val) {
        case '00':
          return 'New';
        case '02':
          return 'Pending Active';
        case '03':
          return 'Active';
        case '04':
          return 'Suspended';
        case '05':
          return 'Frozen';
        case '06':
          return 'Closed';
        case '07':
          return 'Capped';
        case '08':
          return 'Dormant';
        default:
          return;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.entity-search-dialog {
  .dialog-form-search {
    /deep/.form-flex-container {
      padding: 0;
    }
  }
}
.option-buttons {
  margin-top: 20px;
  display: flex;
  flex-flow: row-reverse nowrap;
  /deep/ .el-button {
    margin-left: 8px;
  }
}
</style>
