<template>
  <div class="account-statemnet">
    <portal-header :projectObj="titleConfig" />
    <section>
      <div class="section-title margin-left-20 margin-right-20 none-padding-left none-padding-right">
        {{ $t('IDC.accountStatement.AccountInfo') }}
      </div>
      <div class="account-type">
        <el-form size="small" :rules="accountFormRules" :model="accountForm" ref="accountForm">
          <el-row :gutter="20">
            <el-col :xs="colSmall" :sm="colSmall" :md="colSmall" :lg="colSmall" :xl="colXL">
              <el-form-item
                class="none-margin-bottom"
                :label="$t('IDC.accountStatement.accountType')" prop="accountType">
                <el-select
                  :placeholder="$t('common.placeholder.select')"
                  v-model="accountForm.accountType"
                  @change="selectAccountType"
                >
                  <el-option
                    v-for="item in accountTypeForm"
                    :key="item.value"
                    :value="item.value"
                    :label="item.label"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <basic-info-item :basicInfo="currentAccount[0]" v-if="accountForm.accountType"></basic-info-item>
        <div v-if="isIdentityHotAccount">
          <div style="padding-bottom: 20px; padding-top: 20px">Unaccounted Transactions</div>
          <table-page
            id="table"
            size="small"
            :height="280"
            :columns="AccountColumns"
            :pagination="paginationConfig"
            :outSideTableData="outSideTableData"
            :initializingLoading="false"
            :is-search="isSearchTableData"
          >
            <template slot="transactionAmount" slot-scope="scope">
              <div class="align-right">{{ scope.data.row.accountingAmount.amount }}</div>
            </template>
          </table-page>
        </div>
      </div>
    </section>

    <section>
      <div class="section-title margin-bottom-20">{{ $t('IDC.accountStatement.AccountedTransaction') }}</div>
      <div class="section-content">
        <portal-form
          ref="searchList"
          :form-data="formData"
          :form-config-list="formConfigList"
          :searchBtnLoading="searchLoading"
          :form-rules="tempFormRules"
          :searchBtnDisabled="isSearchBtnDisabled"
          @common-enter-event="search"
          @reset-btn-click="reset"
          @search-btn-click="search"
        >
          <!-- 新需求 -->
          <template v-slot:oppositeParty>
            <el-row :class="['grouped-input']" :gutter="4">
              <el-col :class="['grouped-input-type']" :span="handleSpanWidth">
                <el-form-item :label="$t('IDC.accountStatement.oppositeParty')" prop="oppositePartyType">
                  <el-select
                    v-model="formData.oppositePartyType"
                    :placeholder="$t('common.placeholder.select')"
                    @change="handleSelect"
                  >
                    <el-option
                      v-for="item in partySelectOptions"
                      :label="item.label"
                      :key="item.value"
                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :class="['grouped-input-value']" :span="16" v-if="showRightCode">
                <el-form-item
                  :class="['hidden-label']"
                  :label="oppositeInputTitleLabel"
                  prop="oppositeParty"
                  :required="oppositePartyRequired"
                >
                  <el-input
                    :placeholder="oppositeInputTitleLabel"
                    v-model="formData.oppositeParty"
                    class="input-with-select"
                    readonly="readonly"
                    @blur="debitPartyShortCodeBlur($event)"
                  >
                    <i
                      v-if="formData.oppositePartyType !== 'all' || formData.oppositePartyType !== '8000'"
                      class="el-icon-more icon-cursor"
                      slot="suffix"
                      @click="openDebitShortCodeDialog"
                    >
                    </i>
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </template>
        </portal-form>
      </div>
      <div class="section-content padding-top-20">
        <el-dropdown @command="handleExport" style="padding-bottom: 20px">
          <el-button
            type="primary"
            size="small"
            v-preventReClick
            :disabled="!hasPerm('IC_DOWNLOAD_DAILY_ACCOUNT_STATEMENT_BTN')"
          >
            {{ $t('common.button.export') }}
            <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="Excel">Excel</el-dropdown-item>
            <el-dropdown-item command="Excel2007">Excel 2007</el-dropdown-item>
            <el-dropdown-item command="CSV">CSV</el-dropdown-item>
            <el-dropdown-item command="PDF">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <table-page
          size="small"
          :columns="Columns"
          :has-pagination="true"
          :pagination="paginationConfig"
          :getTableData="getTableData"
          :is-search="refreshTableFlag"
          :initializingLoading="false"
        >
          <template slot="orderIdOperation" slot-scope="scope">
            <el-button type="text" @click="showDetail(scope.data.row)" style="padding: 0">
              {{ scope.data.row.tradeOrderId }}
            </el-button>
          </template>
          <template slot="operation" slot-scope="scope">
            <el-button
              type="text"
              size="small"
              v-if="isShowReverse(scope.data.row)"
              class="btn"
              @click="reverse(scope.data.row)"
            >
              {{ $t('IDC.reviewTransaction.label.reverse') }}
            </el-button>
          </template>
        </table-page>
      </div>
    </section>
    <entity-search-dialog
      :dialog-title="entityDialogTitle"
      :dialogVisible.sync="dialogVisible"
      :fieldName="inputField"
      @select="receiverSelected"
      @onCloseDialog="closeCallBack"
    >
    </entity-search-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header/index.vue';
import BasicInfoItem from '../components/basic-info-item.vue';
import PortalForm from '@/components/portal-form/index.vue';
import tablePage from '@/components/tablePage/index.vue';
import cookie from '@/common/utils/cookie';
import { dateFormat, formatDateFromArray } from '@/common/utils/date-time-utils';
import EntitySearchDialog from '../components/entity-search-dialog.vue';
import { getLimitDay } from '@/views/identifier-center/api/api-audit-log';
import {
  queryAccountList,
  getStatementList,
  getUnFinishHotEntry,
  exportOrderList,
} from '@/views/identifier-center/api/api-transaction.js';

const Identity_type = {
  all: 'all',
  customer: '1000',
  group: '7000',
  organization: '5000',
  sp: '8000',
};

export default {
  components: {
    PortalHeader,
    BasicInfoItem,
    PortalForm,
    tablePage,
    EntitySearchDialog,
  },
  data() {
    return {
      routeType: '',
      dialogVisible: false,
      entityDialogTitle: '',
      inputField: '',
      handleSpanWidth: 24,
      showRightCode: false,
      partySelectOptions: [
        {
          label: 'All',
          value: Identity_type.all,
        },
        {
          label: 'Organization',
          value: Identity_type.organization,
        },
        {
          label: 'Customer',
          value: Identity_type.customer,
        },
        {
          label: 'Group',
          value: Identity_type.group,
        },
        {
          label: 'SP',
          value: Identity_type.sp,
        },
      ],
      outSideTableData: [],
      oppositeInputTitleLabel: '',
      oppositePartyRequired: true,
      isSearchTableData: false,
      isIdentityHotAccount: false,
      tablePageShow: false,
      refreshTableFlag: false,
      accountForm: {
        accountType: '',
      },
      titleConfig: {
        title: this.$t('IDC.accountStatement.title'),
      },
      accountFormRules: {
        accountType: [{ required: true }],
      },
      accountTypeForm: '',
      accountList: [],
      currentAccount: {},
      formData: {
        completionTime: '',
        action: '',
        oppositeParty: '',
        oppositePartyId: '',
        oppositePartyType: 'all',
      },
      tempFormRules: {
        completionTime: [{ required: true }],
      },
      searchLoading: false,
      paginationConfig: {
        pageIndex: 1,
        pageSize: 10,
      },
      checkboxOptions: [
        {
          label: 'Both',
          value: '',
        },
        {
          label: 'Debit',
          value: 1,
        },
        {
          label: 'Credit',
          value: 0,
        },
      ],
      oppositePartyOptions: [
        {
          label: 'All',
          value: '',
        },
        {
          label: 'Customer',
          value: '1000',
        },
        {
          label: 'Group',
          value: '7000',
        },
        {
          label: 'Organization',
          value: '5000',
        },
        {
          label: 'SP',
          value: '8000',
        },
      ],
      AccountColumns: [
        {
          label: this.$t('IDC.accountStatement.accountNo'),
          prop: 'accountNo',
          'min-width': '150px',
          align: 'left',
        },
        {
          label: this.$t('IDC.accountStatement.initiationTime'),
          prop: 'createTime',
          'min-width': '150px',
          align: 'left',
        },
        {
          label: this.$t('IDC.accountStatement.TransactionAmount'),
          prop: 'transactionAmount',
          __slotName: 'transactionAmount',
          'min-width': '150px',
          align: 'left',
        },
      ],
      Columns: [
        {
          label: 'Receipt No.',
          prop: 'tradeOrderId',
          'min-width': '310px',
          align: 'left',
          __slotName: 'orderIdOperation',
        },
        { label: 'Completion Time', prop: 'createTime', 'min-width': '150px', align: 'left' },
        { label: 'Details', prop: 'transactionDetails', 'min-width': '150px', align: 'left' },
        { label: 'Opposite Party', prop: 'opposityPartyName', 'min-width': '150px', align: 'left' },
        { label: 'Transaction Status', prop: 'transactionStatus', 'min-width': '150px', align: 'left' },
        { label: 'Currency', prop: 'transactionCurrency', 'min-width': '150px', align: 'left' },
        { label: 'Transaction Amount', prop: 'transactionAmount', 'min-width': '150px', align: 'left' },
        { label: 'Balance', prop: 'balance', 'min-width': '150px', align: 'left' },
        {
          label: 'Operation',
          prop: 'operation',
          'min-width': '150px',
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        },
      ],
      limitDays: {},
      choiceDate: '',
      pickerOptions: {
        // 选择开始时间
        onPick: ({ maxDate, minDate }) => {
          if (minDate && !maxDate) {
            this.choiceDate = minDate.getTime();
          }
          if (maxDate) {
            this.choiceDate = '';
          }
        },
        disabledDate: (time) => {
          let todayEndTime = new Date(dateFormat(new Date().getTime(), 'YYYY-MM-DD') + ' 23:59:59').getTime();
          if (!this.isNull(this.choiceDate)) {
            const one = this.limitDays.maxDay * 24 * 3600 * 1000;
            let maxTime = this.choiceDate + one - 1;
            maxTime = maxTime > todayEndTime ? todayEndTime : maxTime;
            const minTime = this.choiceDate - one + 1;
            return time.getTime() < minTime || time.getTime() > maxTime;
          } else {
            return time.getTime() > todayEndTime;
          }
        },
      },
    };
  },
  computed: {
    isSearchBtnDisabled() {
      const isTransactionAccountStatementPage = this.routeType === 'transactionCenter.accountStatement';
      const hasPermission = this.hasPerm('IC_QUERY_DAILY_ACCOUNT_STATEMENT_BTN');

      return isTransactionAccountStatementPage && !hasPermission;
    },
    isExportBtnShow() {
      const isTransactionAccountStatementPage = this.routeType === 'transactionCenter.accountStatement';

      return isTransactionAccountStatementPage;
    },
    identityId() {
      if (this.$route.query.data) {
        const queryData = decodeURIComponent(this.$route.query.data);
        return JSON.parse(queryData).identityId || '';
      } else {
        return cookie.get('organizationIdentityId') || '';
      }
    },
    formConfigList() {
      return [
        {
          label: 'Completion Time',
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          description: this.$t('', { obj: this.limitDays.maxDay }),
          prop: 'completionTime',
          comp: [
            {
              key: 'completionTime',
              comp: 'el-date-picker',
              bind: {
                placeholder: this.$t('common.placeholder.enter'),
                align: 'right',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'picker-options': this.pickerOptions,
                'range-separator': '~',
                'value-format': 'yyyy-MM-dd HH:mm:ss',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
        {
          label: 'Debit/Credit',
          key: 'action',
          prop: 'action',
          options: this.checkboxOptions,
          comp: 'el-select',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          slotName: 'oppositeParty',
        },
      ];
    },
  },
  watch: {
    'formData.oppositePartyType': {
      handler(value) {
        this.formData.oppositeParty = '';
        this.formData.oppositePartyId = '';
        if (value === Identity_type.all) {
          this.showRightCode = false;
          this.handleSpanWidth = 24;
        }
        if (value === Identity_type.organization) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.shortCode');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.customer) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.MSISDN');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.group) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.groupCode');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.sp) {
          this.handleSpanWidth = 24;
          this.showRightCode = false;
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {
    this.getAccountData();
    this.getLimitDay();
    this.routeType = this.$route.meta.type;
  },
  methods: {
    isNull(value) {
      return !value;
    },
    async getLimitDay() {
      let res = await getLimitDay();
      if (res) {
        this.limitDays = { maxDay: 7, defaultDay: 6 };
        this.getdefaultTime(this.limitDays.defaultDay);
      }
    },
    getdefaultTime(time) {
      this.formData.startTime = dateFormat(new Date().getTime() - 3600 * 1000 * 24 * time, 'YYYY-MM-DD HH:mm:ss');
      this.formData.endTime = dateFormat(new Date().getTime(), 'YYYY-MM-DD HH:mm:ss');
      this.formData.completionTime = [this.formData.startTime, this.formData.endTime];
    },
    openDebitShortCodeDialog() {
      if (
        this.formData.oppositePartyType === '1000' ||
        this.formData.oppositePartyType === '5000' ||
        this.formData.oppositePartyType === '7000'
      ) {
        this.dialogVisible = true;
      } else {
        this.dialogVisible = false;
      }
    },
    debitPartyShortCodeBlur(event) {
      if (!event.target.value) {
        return;
      }
    },
    isShowReverse(row) {
      return row.reverseMode && (row.transactionStatus === 'COMPLETED' || row.transactionStatus === 'PAID');
    },
    selectAccountType(val) {
      this.currentAccount = this.accountList.filter((item) => {
        return item.accountTypeId === val;
      });
      this.isIdentityHotAccount = this.currentAccount[0].isIdentityHotAccount;
      if (this.isIdentityHotAccount) {
        this.isSearchTableData = true;
        this.getUnFinishHotEntryList();
      }
    },
    async getAccountData() {
      const params = {
        identityId: this.identityId,
        dataScope: ['ALL'],
      };

      const res = await queryAccountList(params);
      if (!res) {
        return;
      }
      this.accountList = res.list;
      this.accountTypeForm = res.list.map((item) => {
        return { label: item.accountName + '/' + item.accountNo, value: item.accountTypeId };
      });
    },
    getUnFinishHotEntryList() {
      return new Promise((resolve) => {
        const { accountNo, accountTypeId } = this.currentAccount[0];
        let paramsObj = {
          identityId: this.identityId,
          accountNo,
          accountTypeId,
          pageNo: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
        };
        getUnFinishHotEntry(paramsObj)
          .then((res) => {
            if (res.header && res.header.success) {
              this.outSideTableData = res.accountEntryDTOList || [];
              this.outSideTableData.forEach((item) => {
                item.createTime = formatDateFromArray(item.createTime, 'DD/MM/YYYY HH:mm:ss');
              });
              resolve(res);
            } else {
              return false;
            }
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.isSearchTableData = false;
          });
      });
    },

    // 新需求 获取实体信息相关代码 oppositePartyId
    receiverSelected(val) {
      const objMap = {
        msisdn: 'identityId',
        shortCode: 'organizationId',
        groupCode: 'identityId',
      };
      this.formData.oppositeParty = val[this.inputField];
      this.formData.oppositePartyId = val[objMap[this.inputField]];
    },
    closeCallBack(val) {
      this.dialogVisible = val;
    },

    // 弹窗标题
    handleSelect(entityCode) {
      this.formData.oppositeParty = '';
      this.formData.oppositePartyId = '';
      switch (entityCode) {
        case '1000':
          this.entityDialogTitle = 'Select Customer';
          this.inputField = 'msisdn';
          break;
        case '5000':
          this.entityDialogTitle = 'Select Organization'; // Short Code
          this.inputField = 'shortCode';
          break;
        case '7000':
          this.entityDialogTitle = 'Select Group Code';
          this.inputField = 'groupCode';
          break;
      }
    },
    search() {
      this.$refs.accountForm.validate((valid) => {
        if (valid) {
          this.$refs.searchList.validate((val) => {
            if (val) {
              // 点击search起始页应该为1
              this.paginationConfig.pageIndex = 1;
              this.refreshTableFlag = true;
              this.searchLoading = true;
            }
          });
        }
      });
    },

    reset() {
      this.$refs.searchList.resetFields();
      this.getLimitDay();
      this.paginationConfig.pageIndex = 1;
      this.refreshTableFlag = true;
    },
    showDetail(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.identityId,
        identityType: '5000',
      };

      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    reverse(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.identityId,
        identityType: '5000',
      };

      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    getTableData() {
      return new Promise((resolve) => {
        const { accountNo, accountTypeId } = this.currentAccount[0];
        let paramsObj = {
          tradePartyId: this.identityId,
          identityType: '5000',
          accountNo,
          accountTypeId,
          oppositePartyId: this.formData.oppositePartyId,
          oppositePartyType: this.formData.oppositePartyType === '8000' ? this.formData.oppositePartyType : '',
          completionTimeBegin: this.formData.completionTime[0],
          completionTimeEnd: this.formData.completionTime[1],
          transactionRole: this.formData.action.toString(),
          pageNo: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
          startNumber: this.paginationConfig.pageIndex,
        };
        getStatementList(paramsObj)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.spOrderQueryDTOList || [];
              res.totalRecords = res.totalSize;
            } else {
              return false;
            }
            resolve(res);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
    handleExport(fileType) {
      let params = {
        tradePartyId: this.identityId,
        identityType: '5000',
        accountType: this.currentAccount.length ? this.currentAccount[0].accountTypeName : '', // hudi胡迪 说不要accountTypeId 要accountName
        accountNo: this.currentAccount.length ? this.currentAccount[0].accountNo : '',
        oppositePartyId: this.formData.oppositePartyId,
        oppositePartyType: this.formData.oppositePartyType == '8000' ? this.formData.oppositePartyType : '',
        transactionRole: this.formData.action.toString(),
        initiatorIdentityId: '',
        serviceCodes: [],
        status: [],
        createTimeBegin: this.formData.completionTime[0],
        createTimeEnd: this.formData.completionTime[1],
        exportType: fileType,
        requestType: 'accountTran',
        reasonType: '',
        currency: '',
      };
      exportOrderList(params)
        .then((res) => {
          if (fileType === 'Excel2007') {
            fileType = 'XLS';
          } else if (fileType === 'Excel') {
            fileType = 'XLSX';
          }
          if (res.type === 'application/json') {
            const reader = new FileReader();
            reader.readAsText(res);
            const _this = this;
            reader.onload = function () {
              const errorData = JSON.parse(reader.result);
              _this.$modal.msgError(errorData.header.responseDesc);
            };
          } else {
            const start = new Date();
            const date = dateFormat(start, 'YYYYMMDDHHmmss');
            const blob = new Blob([res], { type: 'application/ms-excel' }); // 解析后端返回的乱码
            const elink = document.createElement('a');
            elink.download = 'TRANSACTION_' + date + '.' + fileType.toLowerCase(); // 定义下载名字
            elink.style.display = 'none'; // 决定是否隐藏
            elink.href = URL.createObjectURL(blob);
            document.body.appendChild(elink);
            elink.click();
            URL.revokeObjectURL(elink.href); // 释放URL 对象
            document.body.removeChild(elink);
          }
        })
        .catch()
        .finally();
    },
  },
};
</script>

