  .none-padding-left {
    padding-left: 0 !important;
  }
  .none-padding-right {
    padding-right: 0 !important;
  }
  .padding-top-20 {
    padding-top: 20px !important;
  }
  background: transparent;
  border-radius: 4px;
  /deep/.el-divider--horizontal {
    margin: 0;
  }
  /deep/ .el-form .el-form-item .el-form-item__label {
    color: $--fill_color--emphasis;
  }
  /deep/.form-flex-container {
    padding: 0;
  }
  section {
    background: $--text_color--white;
    margin-bottom: 16px;
    padding-bottom: 24px;
    .none-margin-bottom {
      margin-bottom: 0;
    }
    .section-title {
      color: $--text_color--emphasis;
      font-family: 'HuaweiSans-Medium';
      box-shadow: 0 1px 0 $--fill_color--regular;
      margin-bottom: 20px;
      padding: 20px 20px 12px;
    }
    .account-type {
      padding: 0 20px;
      /deep/ .el-form-item__label {
        margin-bottom: 8px;
        line-height: 19px;
      }
    }
    .section-content {
      padding: 0 20px;
    }
  }
  .grouped-input {
    .grouped-input-type {
      /deep/.el-form-item {
        &.hidden-label {
          .el-form-item__label {
            visibility: hidden;
          }
        }
        .el-form-item__label {
          overflow: visible;
          white-space: nowrap;
        }
      }
    }

    .grouped-input-value {
