<template>
  <div>
    <div class="basic-info-box">
      <el-row :gutter="20">
        <el-col v-bind="colOptions" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">{{ item.label }}</div>
            <div class="basic-info-card__value">{{ item.value }}</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoItem',

  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      propsOrder: [
        {
          name: 'accountNo',
          label: this.$t('IDC.accountStatement.accountNo'),
        },
        {
          name: 'accountName',
          label: this.$t('IDC.accountStatement.accountType'),
        },
        {
          name: 'accountAlias',
          label: this.$t('IDC.accountStatement.alias'),
        },
        {
          name: 'currency',
          label: this.$t('IDC.accountStatement.currency'),
        },
        {
          name: 'relationship',
          label: this.$t('IDC.accountStatement.accountRelationship'),
        },
        {
          name: 'balance',
          label: this.$t('IDC.accountStatement.currentBalance'),
        },
        {
          name: 'availableBalance',
          label: this.$t('IDC.accountStatement.availableBalance'),
        },
        {
          name: 'reservedBalance',
          label: this.$t('IDC.accountStatement.reservedBalance'),
        },
        {
          name: 'unclearBalance',
          label: this.$t('IDC.accountStatement.unclearBalance'),
        },
        {
          name: 'status',
          label: this.$t('IDC.accountStatement.status'),
        },
      ],
    };
  },

  computed: {
    configList() {
      const configMap = this.basicInfo;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
};
</script>
