  .basic-info-card {
    padding-bottom: 20px;
    font-size: 14px;

    &__label {
      margin-bottom: 8px;
      color: $--text_color--secondary;
    }

    .basic-info-card__value {
      color: $--fill_color--emphasis;
      font-family: Huawei Sans;
      font-size: 14px;
    }
  }
}
</style>
