import request from '@/common/utils/request';

export function getCategoryList() {
  return request({
    url: '/fintech/v1/auditLog/getCategoryList',
    method: 'get',
  });
}
export function queryEventGroup(data) {
  return request({
    url: '/fintech/v1/auditLog/queryEventGroup',
    method: 'post',
    data: data,
  });
}
export function queryEventType(data) {
  return request({
    url: '/fintech/v1/auditLog/queryEventType',
    method: 'post',
    data,
  });
}
export function getLimitDay() {
  return request({
    url: '/fintech/v1/auditLog/getLimitDay',
    method: 'get',
  });
}
export function getListAuditLog(data) {
  return request({
    url: '/fintech/v1/auditLog/listAuditLog',
    method: 'post',
    data,
  });
}
export function queryAuditLogDetail(data) {
  return request({
    url: '/fintech/v1/auditLog/queryAuditLogDetail',
    method: 'post',
    data,
  });
}
export function listTakeCallReferenceAuditLog(data) {
  return request({
    url: '/fintech/v1/auditLog/listTakeCallReferenceAuditLog  ',
    method: 'post',
    data,
  });
}
export function downloadAuditLog(data) {
  return request({
    url: '/fintech/v1/auditLog/downloadAuditLog',
    method: 'post',
    needHeader: true,
    data,
    responseType: 'blob',
  });
}
